<template>
  <div class="player-list">
    <van-nav-bar
      :title="`播放列表(${songList.length})`"
      right-text="关闭"
      @click-right="showList"
    />
    <scroll-view class="scroll-view" :data="songList">
      <ul>
        <li :class="{active: i === currentIndex}" :key="i"
          v-for="(e, i) in songList"
        >
          <van-icon name="music-o" />
          <p @click="play(i)">{{e.songname}}<span>{{e.singer[0].name}}</span></p>
          <van-icon class="like" :name="e.liked?'like':'like-o'" @click="collect(i)"/>
          <van-icon name="cross" @click="remove(i)"/>
        </li>
      </ul>
    </scroll-view>
  </div>
</template>
<script>
import ScrollView from '#/ScrollView'
import { mapMutations, mapState, mapActions } from 'vuex'
export default {
  components: {
    ScrollView,
  },
  methods: {
    ...mapMutations('music', ['collection']),
    ...mapMutations('player', ['liked']),
    ...mapMutations('player', ['showList', 'showDetail', 'removeSongList']),
    ...mapActions('player', ['play']),
    remove(i) {
      this.$dialog.confirm({
        message: '确定把歌曲从当前播放列表移除？'
      }).then(_=> {
        this.removeSongList(i)
      }).catch(_=> {

      })
    },
    collect(i) {
      let e = this.songList[i]
      this.collection({
        mid: e.songmid,
        song: e.songname,
        singer: e.singer[0].name
      })
      this.liked(i)
    }
  },
  computed: {
    ...mapState('player', ['songList', 'currentIndex'])
  },
}
</script>
<style lang="scss" scoped>
/deep/ .van-icon:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
/deep/ .van-icon {
  flex: 0 0 .6rem;
  font-size: .35rem; }
/deep/ .active .van-icon-music-o {
  transition: .5s;
  animation: myRotate 3s infinite linear; }
@keyframes myRotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
.player-list {
  z-index: 3;
  position: fixed;
  width: 100%;
  height: 100vh;
  bottom: 0;
  left: 0;
  background-color: #fff;
  .scroll-view {
    position: relative;
    height: calc(100vh - 44px);
    overflow: hidden;
    li {
      display: flex;
      height: .8rem;
      line-height: .8rem;
      justify-content: space-between;
      font-size: 15px;
      padding: 0 .2rem;
      border-bottom: 1px solid #f6f6f6;
      p {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        flex: auto;
      }
      .like {
        color: #ff0000;
      }
      &.active {
        color: #ff0000;
        background-color: #f6f6f6;
        span {
          color: #ff0000; } }
      span {
        color: #aaa;
        margin-left: .2rem;
        font-size: 14px; } } } }

</style>



