<template>
  <div id="app">
    <keep-alive>
      <router-view/>
    </keep-alive>
    <min-player v-show="currentIndex !== -1" />
    <player v-show="showDetail" />
    <transition name="list">
      <player-list v-show="showList" />
    </transition>
  </div>
</template>
<script>
import MinPlayer from '#/MinPlayer'
import Player from '@/page/Player'
import PlayerList from '#/PlayerList'
import { mapState } from 'vuex'
export default {
  name: 'App',
  components: {
    MinPlayer,
    Player,
    PlayerList,
  },
  computed: {
    ...mapState('player', ['showList', 'showDetail', 'currentIndex'])
  }
}
</script>
<style>
.list-enter {
  opacity: .5;
  transform: translateY(50%);
}
.list-enter-active {
  transition: .1s ease;
}
.list-enter-to {
  opacity: 1;
  transform: translateY(0);
}
.list-leave {
  transform: translateY(0%);
}
.list-leave-active {
  transition: .3s ease;
}
.list-leave-to {
  opacity: 1;
  transform: translateY(100%);
}
</style>



