import { Dialog, Toast } from 'vant'

const state = {
  showList: false,
  showDetail: false,
  isPlay: false,
  songList: [],
  currentIndex: -1,
  loading: true,
  player: new QMplayer({
    onplay() {
      state.loading = false
    },
    onended() {
      state.currentIndex++
    }
  }),
}
const mutations = {
  showList(state) {
    state.showList = !state.showList
  },
  showDetail(state) {
    Toast('歌词开发中')
  },
  songList(state, val) {
    state.songList = val
    state.currentIndex = -1
  },
  liked(state, idx) {
    let one = state.songList[idx]
    one.liked = !one.liked
    state.songList.splice(idx, 1, one)
  },
  addSongList(state, val) {
    state.songList.push(...val)
  },
  removeSongList(state, idx) {
    if (state.currentIndex === idx) {
      state.player.pause()
    }
    state.songList.splice(idx, 1)
    state.player.playNext()
  },
  currentIndex(state, val) {
    state.currentIndex = val
  },
  toggle(state) {
    state.isPlay = !state.isPlay
    state.player.toggle()
  },
}
const getters = {
  songIdList(state) {
    return state.songList.map(e => e.songmid)
  },
  getInfo(state) {
    return state.songList[state.player.data.index]
  },
}
const actions = {
  play({ state, getters }, idx=0) {
    if (idx === state.currentIndex) return
    state.currentIndex = idx
    state.player.play(getters.songIdList, {
      index: state.currentIndex
    })
    state.loading = true
    state.isPlay = true
    let timer = setTimeout(_=> {
      if (state.loading) {
        Dialog.alert({
          message: '加载失败！',
        }).then(_=> {
          state.currentIndex = -1
        })
      }
      clearTimeout(timer)
    }, 5000)
  },
}
export default {
  namespaced: true,
  actions,
  state,
  getters,
  mutations,
}


